import React from 'react';
import { Link, NavLink } from 'react-router-dom';
import { useState, useEffect } from 'react';

function MainMenu() {
  // Mobile Submenu Trigger Show after 992
  const [showLink, setShowLink] = useState(false);
  useEffect(() => {
    function handleResize() {
      if (window.innerWidth < 992) {
        setShowLink(true);
      } else {
        setShowLink(false);
      }
    }
    window.addEventListener('resize', handleResize);
    handleResize(); // Check on initial render
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Menu Trigger Add Remove Class
  const [activeItems, setActiveItems] = useState([]);
  const toggleItem = (index) => {
    if (activeItems.includes(index)) {
      setActiveItems(activeItems.filter((item) => item !== index));
    } else {
      setActiveItems([...activeItems, index]);
    }
  };

  return (
    <>
      <ul className="main-navbar">
        {/* Home */}
        <li
          key="0"
          className={`has-dropdown ${
            activeItems.includes(0) ? 'menu-active' : ''
          }`}
        >
          <Link to="/">
            Home{' '}
            {showLink && (
              <span onClick={() => toggleItem(0)} className="submenu-trigger">
                <i className="fa-solid fa-angle-down"></i>
              </span>
            )}
          </Link>
          
        </li>

        {/* Pages */}
        <li
          key="1"
          className={`has-dropdown ${
            activeItems.includes(1) ? 'menu-active' : ''
          }`}
        >
          <Link to="/about-us">
            Our Vision{' '}
            {showLink && (
              <span onClick={() => toggleItem(1)} className="submenu-trigger">
                <i className="fa-solid fa-angle-down"></i>
              </span>
            )}
          </Link>
       
        </li>

        {/* Company */}
        <li
          key="2"
          className={`has-dropdown ${
            activeItems.includes(2) ? 'menu-active' : ''
          }`}
        >
          <Link to="/services">
            Services{' '}
            {showLink && (
              <span onClick={() => toggleItem(2)} className="submenu-trigger">
                <i className="fa-solid fa-angle-down"></i>
              </span>
            )}
          </Link>
          <ul className="submenu">
            
            <li key="com-1">
              <NavLink to="/service-details/1">Web Development</NavLink>
            </li>
            <li key="com-2">
              <NavLink to="/service-details/3">Digital Marketing</NavLink>
            </li>
          </ul>
        </li>

        {/* Industries */}
        <li
          key="3"
          className={`has-dropdown ${
            activeItems.includes(3) ? 'menu-active' : ''
          }`}
        >
          <Link to="/deals">
            Deals{' '}
            {showLink && (
              <span onClick={() => toggleItem(3)} className="submenu-trigger">
                <i className="fa-solid fa-angle-down"></i>
              </span>
            )}
          </Link>
         
        </li>

       
      </ul>
    </>
  );
}

export default MainMenu;
